.ball-scale {
    position: relative;
    -webkit-animation: ball-scale-inner 3s 0s ease infinite;
            animation: ball-scale-inner 3s 0s ease infinite;
}
.ball-scale::after,
.ball-scale::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 3.65vw;
    border: 1px solid rgba(0, 255, 255, 0.5);
    box-shadow: 0 0 5px #00ffde inset;
    -webkit-animation: ball-scale 2s 0s ease infinite;
            animation: ball-scale 2s 0s ease infinite;
}
.ball-scale::after {
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

@media screen and (min-width: 750px)  {
    .ball-scale::after,
    .ball-scale::before {
        display: none;
    }
}

@-webkit-keyframes ball-scale {
    0% {
        -webkit-transform: scale(1.05, 1.05);
                transform: scale(1.05, 1.05);
        opacity: 1;
    }

    90% {
        -webkit-transform: scale(1.15, 1.4);
                transform: scale(1.15, 1.4);
        opacity: 0
    }

    100% {
        -webkit-transform: scale(1.05, 1.2);
                transform: scale(1.05, 1.2);
        opacity: 0
    }
}

@keyframes ball-scale {
    0% {
        -webkit-transform: scale(1.05, 1.05);
                transform: scale(1.05, 1.05);
        opacity: 1;
    }

    90% {
        -webkit-transform: scale(1.15, 1.4);
                transform: scale(1.15, 1.4);
        opacity: 0
    }

    100% {
        -webkit-transform: scale(1.05, 1.2);
                transform: scale(1.05, 1.2);
        opacity: 0
    }
}

@-webkit-keyframes ball-scale-inner {
    0% {
        -webkit-transform: scale(0.98, 0.98);
                transform: scale(0.98, 0.98);
        opacity: 1;
    }

    50% {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
        opacity: 0.9
    }
}

@keyframes ball-scale-inner {
    0% {
        -webkit-transform: scale(0.98, 0.98);
                transform: scale(0.98, 0.98);
        opacity: 1;
    }

    50% {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
        opacity: 0.9
    }
}

